import * as React from "react";
import FormDialog from "./FormDialog";

import {
	Box,
	Divider,
	Typography,
	Card,
	Badge,
	Menu,
	Avatar,
	CardHeader,
	IconButton,
	Tooltip,
} from "@mui/material";

import RemoveCircleOutlineOutlinedIcon from "@mui/icons-material/RemoveCircleOutlineOutlined";
import AddCircleOutlineOutlinedIcon from "@mui/icons-material/AddCircleOutlineOutlined";
import DeleteIcon from "@mui/icons-material/Delete";
import ShoppingBasketOutlinedIcon from "@mui/icons-material/ShoppingBasketOutlined";

export default function DropBasket(props) {
	const [anchorEl, setAnchorEl] = React.useState(null);
	const open = Boolean(anchorEl);
	const handleClick = (event) => {
		setAnchorEl(event.currentTarget);
	};
	const handleClose = () => {
		setAnchorEl(null);
	};

	return (
		<div>
			<Badge badgeContent={props.basket.length} color="success">
				<IconButton
					onClick={handleClick}
					sx={{
						m: -1.5,
					}}
				>
					<ShoppingBasketOutlinedIcon
						sx={{
							fontSize: 30,
						}}
						color="textColor"
					/>
				</IconButton>
			</Badge>

			<Menu
				anchorEl={anchorEl}
				id="account-menu"
				open={open}
				onClose={handleClose}
			>
				<Box sx={{ width: 300 }}>
					<Typography sx={{ textAlign: "center", p: 1 }}>
						
					</Typography>
					<Divider />
					{props.basket.length === 0 ? (
						<Typography sx={{ textAlign: "center", p: 1 }}>
							 
						</Typography>
					) : (
						<>
							{props.basket.map((basketEl) => (
								<Card key={basketEl.item[0]} sx={{ m: 1 }}>
									<CardHeader
										avatar={
											<Avatar
												sx={{ width: 54, height: 54 }}
												aria-label="recipe"
												src={basketEl.item[4]}
											></Avatar>
										}
										title={basketEl.item[1]}
										subheader={basketEl.item[2]}
									/>
									<Box
										sx={{
											display: "flex",
											gap: 2,
											width: "100%",
											justifyContent: "center",
											mb: 1,
										}}
									>
										<Tooltip title="Delete">
											<IconButton
												onClick={() =>
													props.delBasketEl(basketEl.item)
												}
											>
												{basketEl.count === 1 ? (
													<DeleteIcon />
												) : (
													<RemoveCircleOutlineOutlinedIcon />
												)}
											</IconButton>
										</Tooltip>
										<Typography sx={{ textAlign: "center", p: 1 }}>
											{basketEl.count}
										</Typography>
										<Tooltip title="Add">
											<IconButton
												onClick={() =>
													props.addBasket(basketEl.item)
												}
											>
												<AddCircleOutlineOutlinedIcon />
											</IconButton>
										</Tooltip>
									</Box>
								</Card>
							))}
							<Divider />
							<FormDialog
								basket={props.basket}
								submitMessage={props.submitMessage}
							/>
						</>
					)}
				</Box>
			</Menu>
		</div>
	);
}
